
let addSmthBtn = document.querySelectorAll('.add-smth');
let load = document.querySelector('.load');
let body = document.querySelector('.body');
let main = document.querySelector('.main');
let header = document.querySelector('.header');

for (let i = 0; i < addSmthBtn.length; i++) {
    addSmthBtn[i].addEventListener('click', () => {
        load.classList.add('load-active');
        load.classList.remove('load-inactive');
        body.classList.add('body-active');
        main.style.display = 'none';
        header.style.display = 'none';
    });
}

let loadClose = document.querySelector('.load-close');

loadClose.addEventListener('click', () => {
    load.classList.remove('load-active');
    load.classList.add('load-inactive');
    body.classList.remove('body-active');
    main.style.display = 'initial';
    header.style.display = 'initial';
});

/*
    accordeon
*/

let accordeonBtn = document.querySelectorAll('.buttons__item');
let accordeonContent = document.querySelectorAll('.case__item-content');

for (let i = 0; i < accordeonBtn.length; i++) {
    accordeonBtn[i].addEventListener('click', () => {

        accordeonContent.forEach(element => {
            element.classList.add('case-hidden');
            element.classList.remove('case-active');
        });

        if (accordeonContent[i].id === accordeonBtn[i].id) {
            accordeonContent[i].classList.add('case-active');
            accordeonContent[i].classList.remove('case-hidden');
        }

    });
}