
let changePhoto = document.querySelector('.change-photo');
let accountPopup = document.querySelector('.account-load');
let accountPopupClose = document.querySelector('.account-load__close');
let body = document.querySelector('.body');
let main = document.querySelector('.main');
let header = document.querySelector('.header');

changePhoto.addEventListener('click', () => {
    accountPopup.classList.add('load-active');
    accountPopup.classList.remove('load-inactive');
    body.classList.add('body-active');
    main.style.display = 'none';
    header.style.display = 'none';
});

accountPopupClose.addEventListener('click', () => {
    accountPopup.classList.remove('load-active');
    accountPopup.classList.add('load-inactive');
    body.classList.remove('body-active');
    main.style.display = 'initial';
    header.style.display = 'initial';
});